export default {
  data() {
    return {
      user: {
        name: "",
        password: "",
        email: "",
        confirmPassword: "",
        address: {
          street: "",
          city: "",
          zip: "",
        },
      },
      localError: null,
    };
  },
  methods: {
    submit() {
      if (this.user.password !== this.user.confirmPassword) {
        this.localError = "passwords don't match";
        this.$emit("error", this.localError);
        return;
      }
      this.$emit("submitted", this.user);
    },
  },
};
