export default {
  props: ["inloggedUser"],
  data() {
    return {
      user: {
        name: "",
        password: "",
        email: "",
        confirmPassword: "",
        address: {
          street: "",
          city: "",
          zip: "",
        },
      },
      localError: null,
    };
  },
  mounted() {
    if (this.inloggedUser) {
      this.user = { ...this.user, ...this.inloggedUser };
    }
  },
  methods: {
    submit() {
      if (this.user.password !== this.user.confirmPassword) {
        this.localError = "passwords don't match";
        this.$emit("error", this.localError);
        return;
      }
      const userInfo = { ...this.user };
      if (this.user.password === "") {
        delete userInfo.password;
        delete userInfo.confirmPassword;
      }

      console.log("user form updateUserInfoForm: ", userInfo);
      this.$emit("submitted", userInfo);
    },
  },
};
